import { useReducer, useMemo } from 'react'
import produce from 'immer'
import { uniqueId } from 'lodash'

import { Organism, Sample } from '../../types'
import { LocalOrganism, OrganismFilter, EditSampleActions } from './types'
import { useApi } from '../../data/api'
import log from 'loglevel'

const generateLocalId = () => `local/${uniqueId()}`

type ReducerAction =
  | { type: 'reset'; payload: Sample }
  | { type: 'createLocalOrganism'; payload: LocalOrganism }
  | { type: 'updateLocalOrganism'; payload: { id: LocalOrganism['id']; fields: Partial<LocalOrganism> } }
  | { type: 'deleteLocalOrganism'; payload: LocalOrganism }
  | { type: 'createFilter'; payload: OrganismFilter }
  | { type: 'deleteFilter'; payload: OrganismFilter }
  | { type: 'setIsEditing'; payload: boolean }

type ReducerState = {
  organisms: LocalOrganism[]
  organismFilters: OrganismFilter[]
  measurements: boolean
  isEditing: boolean
}

export const initialState = {
  organisms: [],
  organismFilters: [],
  measurements: false,
  isEditing: false,
} as ReducerState

export const editSampleReducer = (state: ReducerState, action: ReducerAction): ReducerState => {
  log.debug('dispatched:', action)

  if (action.type === 'reset') {
    const { payload: sample } = action
    return {
      ...initialState,
      organisms: sample.organisms.map((organism) => ({ ...organism, state: 'saved' })),
      measurements: !!sample.measurements,
    }
  }

  const newState = produce(state, (draft) => {
    switch (action.type) {
      case 'createLocalOrganism': {
        const { payload: organism } = action
        draft.organisms.push({
          ...organism,
          state: 'unsaved',
        })
        break
      }
      case 'updateLocalOrganism': {
        const {
          payload: { id, fields },
        } = action
        const organism = draft.organisms.find((organism) => organism.id === id)
        if (!organism) return
        Object.assign(organism, fields)
        break
      }
      case 'deleteLocalOrganism': {
        const {
          payload: { id },
        } = action
        draft.organisms = draft.organisms.filter((organism) => organism.id !== id)
        break
      }
      case 'createFilter': {
        const {
          payload: { taxonId },
        } = action
        if (state.organismFilters.find((existingFilter) => existingFilter.taxonId === taxonId)) return
        draft.organismFilters.push({ taxonId })
        break
      }
      case 'deleteFilter': {
        const {
          payload: { taxonId },
        } = action
        draft.organismFilters = draft.organismFilters.filter((organismFilter) => organismFilter.taxonId !== taxonId)
        break
      }
      case 'setIsEditing': {
        draft.isEditing = action.payload
        draft.organisms = draft.organisms.filter((organism) => organism.state !== 'unsaved')
        break
      }
    }
  })
  return newState
}

export const useEditSampleReducer = (): [ReducerState, EditSampleActions] => {
  const api = useApi()

  const upsertOrganismIfSavable = async (organism: LocalOrganism) => {
    if (organism.lifeStageId) {
      dispatch({ type: 'updateLocalOrganism', payload: { id: organism.id, fields: { state: 'saving' } } })
      if (!organism.state || organism.state === 'unsaved') {
        const { id } = await api.createOrganism(organism)
        dispatch({ type: 'updateLocalOrganism', payload: { id: organism.id, fields: { id, state: 'saved' } } })
      } else {
        await api.updateOrganism(organism as Organism) // casting as we know organism has a number id
        dispatch({ type: 'updateLocalOrganism', payload: { id: organism.id, fields: { state: 'saved' } } })
      }
    }
  }

  const [state, dispatch] = useReducer(editSampleReducer, initialState)

  const actions = useMemo(
    () => ({
      createOrganism: async ({ taxonId }: Pick<LocalOrganism, 'taxonId'>) => {
        const id = generateLocalId()
        const newOrganism: LocalOrganism = { id, taxonId, splitCount: 0, notes: '', comments: '' }
        dispatch({ type: 'createLocalOrganism', payload: newOrganism })
        await upsertOrganismIfSavable(newOrganism)
      },
      duplicateOrganism: async (organism: LocalOrganism) => {
        const id = generateLocalId()
        const newOrganism: LocalOrganism = {
          id,
          taxonId: organism.taxonId,
          splitCount: 0,
          notes: '',
          comments: '',
        }
        dispatch({ type: 'createLocalOrganism', payload: newOrganism })
        await upsertOrganismIfSavable(newOrganism)
      },
      updateOrganism: async (organism: LocalOrganism) => {
        dispatch({ type: 'updateLocalOrganism', payload: { id: organism.id, fields: organism } })
        await upsertOrganismIfSavable(organism)
      },
      deleteOrganism: async (organism: LocalOrganism) => {
        if (organism.state !== 'unsaved') {
          dispatch({ type: 'updateLocalOrganism', payload: { id: organism.id, fields: { state: 'deleting' } } })
          await api.deleteOrganism(organism as Organism)
        }
        dispatch({ type: 'deleteLocalOrganism', payload: organism })
      },

      createFilter: (filter: OrganismFilter) => dispatch({ type: 'createFilter', payload: filter }),
      deleteFilter: (filter: OrganismFilter) => dispatch({ type: 'deleteFilter', payload: filter }),
      reset: async (sample: Sample) => {
        dispatch({ type: 'reset', payload: sample })
      },
      setIsEditing: (isEditing: boolean) => {
        dispatch({ type: 'setIsEditing', payload: isEditing })
      },
    }),
    [api]
  )
  return [state, actions]
}
