import gql from 'graphql-tag'
import { PaginatedRecords, Sample as GraphSample, SampleTaxaInfo as GraphSampleTaxaInfo } from '@namcbugdb/common'
import { Sample } from '../../types'

export type ResponseData = {
  samples: PaginatedRecords<GraphSample>
  sampleTaxaInfo: PaginatedRecords<GraphSampleTaxaInfo>
}

export const query = gql`
  query Q($sampleId: Int!) {
    samples(sampleIds: [$sampleId]) {
      records {
        sampleId
        boxId
        customerName
        sampleDate
        siteName
        usState
      }
    }
    sampleTaxaInfo(sampleIds: [$sampleId]) {
      records {
        sampleId
        organismId
        taxonomyId
        scientificName
        levelId
        levelName
        splitCount
        lifeStageId
      }
    }
  }
`

export const transformData = (data: ResponseData): Sample => {
  const sample = data.samples.records[0]
  const organisms = data.sampleTaxaInfo.records

  return {
    id: sample.sampleId,
    ...sample,
    measurements: undefined,
    organisms: organisms.map((organism) => ({
      ...organism,
      id: organism.organismId,
      taxonId: organism.taxonomyId,
      notes: organism.notes || '',
      comments: organism.comments || '',
    })),
  }
}
