import { SideNavBar } from '../SideNavBar'
import { ErrorBoundary } from '../ErrorBoundary'
import { Outlet } from 'react-router-dom'

export const routes = [
  {
    path: '/',
    element: (
      <div css={{ height: '100vh', display: 'flex', flexDirection: 'column' }}>
        <AppHeader />
        <SideNavBar />
        <div css={{ flex: 1, padding: '0.5em' }}>
          <ErrorBoundary>
            <Outlet />
          </ErrorBoundary>
        </div>
      </div>
    ),
    children: [
      { path: 'samples', element: <div>/samples</div> },
      { path: 'samples/:sampleId', element: <SampleOverview /> },
      {
        path: 'samples/:sampleId/sorting',
        element: <SampleSorting />,
      },
      {
        path: 'samples/:sampleId/taxonomy',
        element: <SampleTaxonomyIdentification />,
      },
      {
        path: 'samples/:sampleId/sort',
        element: <div>sample sort</div>,
      },
    ],
  },
]
