import React, { Suspense } from 'react'
import { useParams } from 'react-router'
import { useRecoilValue } from 'recoil'

import { selectTaxa, selectLifeStages, selectNoteTypes, selectSampleById, selectMyProfile } from '../../data'
import { SampleTaxonomyIdentification as Component } from './SampleTaxonomyIdentification'
import { Fallback } from './Fallback'

const Connector = (): JSX.Element => {
  const { sampleId } = useParams()

  const taxa = useRecoilValue(selectTaxa)
  const lifeStages = useRecoilValue(selectLifeStages)
  const noteTypes = useRecoilValue(selectNoteTypes)
  const permissions = useRecoilValue(selectMyProfile).permissions
  const sample = useRecoilValue(selectSampleById(Number(sampleId)))

  if (sample === null) return <div>Sample not found.</div>

  return (
    <Component taxa={taxa} lifeStages={lifeStages} noteTypes={noteTypes} sample={sample} permissions={permissions} />
  )
}

export const SampleTaxonomyIdentification = (): JSX.Element => (
  <Suspense fallback={<Fallback />}>
    <Connector />
  </Suspense>
)
